.component.signup-form {
	max-width: 700px;
	width: 100%;

	.row {
		.col {
			padding: 0;
		}
	}

	.form {
		border-radius: $border-rad-xl 0 0 $border-rad-xl;
		height: 500px;
		margin-right: 0;
		box-shadow: $shadow-md;

		.title {

		}
	}

	.welcome {
		width: 100%;
		height: 100%;
		background: url('https://img.freepik.com/free-vector/graphic-design-geometric-wallpaper_52683-34399.jpg?w=2000');
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		border-radius: $border-rad-xl;
		box-shadow: $shadow-md;
	}
}